<template>
  <div id="app">
    <div class="top">
      <div class="desc">
        <div class="left">
          <div class="logo">
            <!--            <van-icon name="@/assets/doctor.svg"/>-->
            <i class="iconfont icon-doctor"></i>
          </div>
          <div style="margin-left: 8px">
            <div class="title" style="font-size: 20px;font-weight: bold;line-height: 30px;">预防接种服务预约</div>
            <div class="tips" style="font-size: 16px;font-weight: normal">预防接种</div>
          </div>
        </div>
        <span style="color: #3485FA;font-size: 20px;font-weight: bold;">已预约</span>
      </div>
      <div class="action">
        <div style="margin-left: auto;"></div>
        <van-button color="#3485FA">撤销</van-button>
        <van-button color="#3485FA">修改预约</van-button>
      </div>
    </div>
    <div class="serviceTime item" @click="showTime = true">
      <div class="topic">服务时间</div>
      <div class="time" @click="showTime = true" style="font-size: 16px;margin-bottom: 12px">
        <span>{{ serviceDate }}</span>
        <span style="font-weight: 600">{{ startTime }} - {{ endTime }}</span>
      </div>
    </div>
    <div class="patients item" @click="showName = true">
      <div class="topic">就诊人</div>
      <div class="name" style="font-size: 18px">{{ name }}</div>
    </div>
    <div class="serviceLog item" @click="showLog = true" style="font-size: 16px;">
      <div class="topic">服务日志</div>
      <div class="log">{{ logDate }} <span style="margin-left: 16px;color: #777;font-size: 16px;">发起预约</span></div>
    </div>
    <van-popup v-model="showTime" position="bottom" :style="{height: '50%'}">
      <van-datetime-picker v-model="serviceTime" key="serviceTime"
                           type="datetime"
                           @confirm="formatTime"

                           title="选择完整时间"/>
    </van-popup>
    <van-popup v-model="showLog" position="bottom" :style="{height: '50%'}">

      <van-datetime-picker v-model="serviceLog" key="serviceLog"
                           @confirm="formatLog"

                           type="datetime"
                           title="选择完整时间"/>
    </van-popup>
    <van-popup v-model="showName">
      <van-field v-model="name" @input="changeName"></van-field>
    </van-popup>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import 'dayjs/locale/zh-cn';

export default {
  name: 'app',
  data() {
    return {
      name: localStorage.getItem('name') || '',
      showName: false,
      serviceTime: '',
      serviceDate: localStorage.getItem('serviceDate') || '',
      startTime: localStorage.getItem('startTime') || '',
      endTime: localStorage.getItem('endTime') || '',
      serviceLog: '',
      log: '',
      logDate: localStorage.getItem('logDate') || '',
      showTime: false,
      showLog: false
    }
  },
  created() {
    dayjs.locale('zh-cn')
  },
  methods: {
    changeName(e) {
      localStorage.setItem('name', e)
    },
    formatTime() {
      this.serviceDate = dayjs(this.serviceTime).locale('zh-cn').format(`YYYY年M月D日（dddd）`)
      this.startTime = dayjs(this.serviceTime).format(`hh:mm`);
      const endTime = new Date(this.serviceTime).getTime() + 1800000;
      this.endTime = dayjs(endTime).format(`hh:mm`);

      localStorage.setItem('serviceDate', this.serviceDate)
      localStorage.setItem('startTime', this.startTime)
      localStorage.setItem('endTime', this.endTime)
      this.showTime = false;
    },
    formatLog() {
      this.logDate = dayjs(this.serviceLog).locale('zh-cn').format(`YYYY年M月D日 hh:mm`)
      localStorage.setItem('logDate', this.logDate)
      this.showLog = false;
    }
  }
}
</script>

